import TotalAmount from '@/components/common/TotalAmount'
import { HTextLabelBox } from '@cetus/ui-kit'
import { convertScientificToDecimal, formatNumber } from '@cetus/utils'
import { fromDecimalsAmount } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { VStack } from '@chakra-ui/react'

type VaultsAddResultProps = {
  preCalculateLoading: boolean
  inputTotalValue?: string
  lpRate: string
  amountLimit?: string
  labelColor?: string
  lpDecimals?: number
  poolName: string
  calculateLpLoading?: boolean
}

export function VaultsAddResult(props: VaultsAddResultProps) {
  const { poolName, amountLimit, lpRate, inputTotalValue, preCalculateLoading, labelColor, lpDecimals, calculateLpLoading } = props

  return (
    <VStack w="100%" gap="20px">
      <TotalAmount totalAmount={inputTotalValue} loading={preCalculateLoading} />

      {/* <HTextLabelBox
        label="Total Amount"
        labelStyle={{
          fontSize: '14px',
          color: labelColor
        }}
        value={formatCurrency(inputTotalValue, 2)}
        valueStyle={{
          fontSize: '14px',
          h: '14px'
        }}
        isLoading={preCalculateLoading}
      /> */}
      <HTextLabelBox
        label="Share of Pool"
        labelStyle={{
          fontSize: '14px',
          color: labelColor
        }}
        value={lpRate}
        isLoading={calculateLpLoading}
        valueStyle={{
          fontSize: '14px',
          h: '14px'
        }}
      />
      <HTextLabelBox
        label="Est. Received LP"
        labelStyle={{
          fontSize: '14px',
          color: labelColor
        }}
        value={`${convertScientificToDecimal(formatNumber(fromDecimalsAmount(amountLimit || '0', lpDecimals ? lpDecimals : 9)).toString(), 9)} ${poolName}`}
        valueStyle={{
          fontSize: '14px',
          h: '14px'
        }}
        isLoading={calculateLpLoading}
      />
    </VStack>
  )
}
